import React, { Component, Fragment } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { animateScroll } from "react-scroll";
export default class DateInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: new Date(),
      isFocused: false
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(date) {
    this.setState({
      startDate: date
    });
  }
  convertDate(date) {
    function pad(s) {
      return s < 10 ? "0" + s : s;
    }
    return [
      pad(date.getDate()),
      pad(date.getMonth() + 1),
      date.getFullYear()
    ].join("/");
  }
  addMessage = () => {
    const { addMessage, message } = this.props;
    const { startDate } = this.state;

    let date = this.convertDate(startDate);
    addMessage(date);
  };
  isFull = () => {
    const { startDate } = this.state;
    if (startDate) return true;
    else return false;
  };
  render() {
    const { isFocused } = this.state;
    const { isIphoneX } = this.props;
    return (
      <Fragment>
        <div
          className="message-input-container"
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "flex-end"
          }}
        >
          <DatePicker
            dateFormat="dd/MM/yyyy"
            selected={this.state.startDate}
            onChange={this.handleChange}
            onFocus={() => {
              this.setState(
                {
                  isFocused: true
                },
                () => {
                  animateScroll.scrollToBottom({
                    duration: 500,
                    smooth: "easeInOutQuad"
                  });
                }
              );
              console.log("focused");
            }}
            onBlur={() => {
              this.setState({
                isFocused: false
              });
              console.log("disfocused");
            }}
          />
          <div
            className={`send-button ${this.isFull() ? "" : "disabled"}`}
            onClick={e => {
              if (this.handleTouchStart) {
                setTimeout(() => {
                  this.handleTouchStart = false;
                }, 1000);
                e.preventDefault();
                return;
              }
              this.handleTouchStart = true;
              this.addMessage();
            }}
          >
            Go
          </div>
        </div>
        {isFocused && (
          <div
            style={{
              height: isIphoneX ? 350 : 300,
              backgroundColor: "#ffffff",
              marginLeft: -20,
              marginRight: -20
            }}
          />
        )}
      </Fragment>
    );
  }
}



