import React, { Component, Fragment } from "react";
import swal from "sweetalert";
import axios from "axios";
import Select from "./Select";
import ToggleButton from "./ToggleButton";
import CardContainer from "./CardContainer";
import { doSMS, clearZero } from "../functions/Auth";
import { postMessage } from "../Utils/middleware";
import { serverUrl } from "../Constants/messages";
import str from "../Constants/localization";
import { animateScroll } from "react-scroll";
import ErrorModal from "./ErrorModal";
import DateInput from "./DateInput";
class MessageInput extends Component {
  state = {
    value: "",
    isFocused: false,
    pin: "",
    phone: "",
    modalIsOpen: false
  };
  handleTouchStart = false;
  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };
  getStaticMessage() {
    const { addMessage, message } = this.props;
    if (Array.isArray(message.message)) {
      return (
        <div className="message-array">
          {message.message.map((message, i) => {
            return (
              <div
                className="message message-static"
                onTouchStart={() => {
                  addMessage({
                    type: "user",
                    ...message
                  });
                }}
                key={i}
              >
                {message.message}
              </div>
            );
          })}
        </div>
      );
    }
    return (
      <div
        className="message message-static"
        onTouchStart={() => {
          addMessage(message);
        }}
      >
        {message.message}
      </div>
    );
  }

  getInputMessage() {
    const { value, isFocused } = this.state;
    const { message, isIphoneX, addMessage } = this.props;
    return (
      <div className="message-input-outer">
        {message.key === "sms" && (
          <div
            className={`button`}
            onClick={e => {
              this.handleTouchStart = true;
              addMessage({
                type: "user",
                message: "I didn't receive a code.",
                inputType: "input",
                key: "no_received"
              });
            }}
          >
            I didn't receive a code
          </div>
        )}
        <div className="message-input-container">
          {message.key === "phone" && <p>+44</p>}

          <input
            type="text"
            value={value}
            placeholder={message.placeholder}
            className={`${message.key === "phone" ? "phone" : ""}`}
            onChange={this.onChange}
            onKeyPress={e => {
              if (e.charCode === 13) this.addMessage();
            }}
            onFocus={() => {
              this.setState(
                {
                  isFocused: true
                },
                () => {
                  animateScroll.scrollToBottom({
                    duration: 500,
                    smooth: "easeInOutQuad"
                  });
                }
              );
            }}
            onBlur={() => {
              this.setState({
                isFocused: false
              });
            }}
          />
          <div
            className={`send-button ${value ? "" : "disabled"}`}
            onTouchEnd={e => {
              if (value) {
                if (this.handleTouchStart) {
                  setTimeout(() => {
                    this.handleTouchStart = false;
                  }, 1000);
                  e.preventDefault();
                  return;
                }
                this.handleTouchStart = true;
                this.addMessage();
              } else alert("All fields are required.");
            }}
          >
            Go
          </div>
        </div>
        {isFocused && (
          <div
            style={{
              height: isIphoneX ? 350 : 300,
              backgroundColor: "#ffffff",
              marginLeft: -20,
              marginRight: -20
            }}
          />
        )}
      </div>
    );
  }
  addDate = date => {
    const { addMessage, message } = this.props;
    postMessage("DOB", date);
    addMessage({
      message: date,
      ...message
    });
  };
  getSelectInput() {
    const { addMessage, message } = this.props;
    const { options } = message;
    return <Select options={options} addMessage={addMessage} />;
  }
  getDateMessage() {
    const { addMessage, logo, message } = this.props;
    return (
      <DateInput addMessage={this.addDate} logo={logo} message={message} />
    );
  }
  setSelectedOption = index => {
    const { addMessage, message } = this.props;
    const selectedOption = message.options[index]["value"];
    postMessage(message.key, selectedOption);
    addMessage({
      type: "user",
      message: selectedOption,
      inputType: "toggleInput"
    });
  };

  getToggleButton() {
    const { options } = this.props.message;
    return (
      <ToggleButton
        options={options}
        setSelectedOption={this.setSelectedOption}
      />
    );
  }

  getCardInput() {
    const { addMessage, message } = this.props;
    const { cards } = message;
    return (
      <CardContainer addMessage={addMessage} cards={cards} message={message} />
    );
  }

  onChange = e => {
    this.setState({
      value: e.target.value
    });
  };

  createPincode = () => {
    return Math.floor(100000 + Math.random() * 900000);
  };
  jsUcfirst(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
  addMessage = () => {
    const { addMessage, message } = this.props;
    const { value } = this.state;
    if (message.key === "email") {
      if (value && !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value))
        swal("Invalid Email", "Please enter valid email address!", "error", {
          confirm: {
            className: "custom-button"
          }
        });
      else if (value) {
        postMessage(message.key, value);
        addMessage({
          type: "user",
          message: value,
          inputType: "input",
          isNext: message.isNext
        });
      } else {
        postMessage(message.key, null);
        addMessage({
          type: "user",
          message: "not now",
          inputType: "input",
          isNext: message.isNext
        });
      }
    } else if (message.key.includes("username")) {
      if (value && !/[a-zA-Z]+(\s|,)[a-zA-Z]{1,19}/.test(value))
        this.setState({
          modalIsOpen: true,
          caption: "Invalid Name",
          content: "Please enter valid FullName!"
        });
      else if (value) {
        let name = value.split(" ");
        let fname = name[0];
        let lname = name[1];
        let updated_name = this.jsUcfirst(fname) + " " + this.jsUcfirst(lname);
        postMessage(message.key, updated_name);
        addMessage({
          type: "user",
          message: updated_name,
          inputType: "input",
          key: message.key,
          isNext: message.isNext
        });
      }
    } else if (message.key === "DOB") {
      if (
        value &&
        !/^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/.test(
          value
        )
      )
        this.setState({
          modalIsOpen: true,
          caption: "Invalid format of date",
          content: "Please enter valid date format!"
        });
      else if (value) {
        postMessage(message.key, value);
        addMessage({
          type: "user",
          message: value,
          inputType: "input",
          isNext: message.isNext
        });
      }
    } else if (message.key !== "email") {
      if (message.key === "phone") {
        let pin = this.createPincode();
        pin = pin.toString();
        let number = clearZero(value);
        let phone = "+44" + number;
        localStorage.setItem("phone", phone);
        localStorage.setItem("pin", pin);
        let response = doSMS(phone, pin);
        console.log("response", response);
      } else postMessage(message.key, value);
      if (message.key === "sms") {
        let phone = localStorage.getItem("phone");
        let pin = localStorage.getItem("pin");
        if (pin === value) {
          postMessage("phone", phone);
          addMessage({
            type: "user",
            message: value,
            key: message.key,
            inputType: "input",
            isNext: message.isNext
          });
        } else {
          addMessage({
            type: "user",
            message: value,
            key: "wrong_sms",
            inputType: "input",
            isNext: message.isNext
          });
        }
      } else
        addMessage({
          type: "user",
          message: value,
          key: message.key,
          inputType: "input",
          isNext: message.isNext
        });
    }
  };

  componentWillLeave(callback) {
    const { message } = this.props;
    if (message.inputType !== "input") {
      this.setState({
        leaving: true
      });
      setTimeout(() => {
        callback();
      }, 400);
    } else callback();
  }

  render() {
    const { leaving, modalIsOpen, caption, content } = this.state;
    const { message } = this.props;
    return (
      <Fragment>
        <ErrorModal
          caption={caption}
          content={content}
          closeModal={this.closeModal}
          modalIsOpen={modalIsOpen}
        />
        {message && (
          <div
            className={`message-input-wrapper ${message.inputType} ${
              leaving ? "leaving" : ""
            }`}
          >
            {message.inputType === "static" ? this.getStaticMessage() : null}
            {message.inputType === "date" ? this.getDateMessage() : null}
            {message.inputType === "input" ? this.getInputMessage() : null}
            {message.inputType === "select" ? this.getSelectInput() : null}
            {message.inputType === "card" ? this.getCardInput() : null}
            {message.inputType === "toggleButton"
              ? this.getToggleButton()
              : null}
          </div>
        )}
      </Fragment>
    );
  }
}

export default MessageInput;



